.tests {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.tests-click-style {
  min-height: calc(80vh - 9.5rem);
}

.tests h2 {
  margin: 2rem 0;
  font-size: 34px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.tests .tests-card:hover .text-box {
  background-color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.tests .tests-card .img-box {
  overflow: hidden;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.tests .tests-card:hover .img-box img {
  transform: scale(1.08);
}

.tests .tests-card .img-box img {
  width: 100%;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  transition: all 0.2s linear;
}

.tests .tests-card .text-box {
  background-color: #edf4f6;
  padding: 1.6rem;
  transition: all 0.2s linear;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.tests .tests-card .text-box .date {
  font-size: 12px;
  color: #484848;
}

.tests .tests-card .text-box h3 {
  font-size: 17px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin: 1rem 0;
}

.tests .tests-card .text-box .card-text {
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 1rem;
}

.tests .tests-card .text-box a {
  display: inline-block;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  padding: 0.5rem;
  background-color: #226500;
  color: #fff;
  border-radius: 4px;
  transition: all 0.18s linear;
  margin-bottom: 0.5rem;
}

.tests .tests-card .text-box a:hover {
  transform: translateY(-4px);
}

.tests .tests-card .text-box a:active {
  transform: translateY(-2px);
}

@media screen and (max-width: 767px) {
  .tests {
    align-items: center;
    padding: 1.5rem;
  }
  .tests .tests-card .text-box button a {
    font-size: 13px;
  }
}
